<template>
    <view class="hang_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">建设分交易</block></cu-custom>
        <view class="things_width_full flex justify-between align-center hang_page_top">
            <view class="current_points">当前建设分 <text class="points_value"> {{userAmount.build_points}}</text> </view>
            <view class="rule">交易规则</view>
        </view>
        <view class="flex justify-center align-start input_value_box">
            <view class="input_value_title">建设分挂卖</view>
            <view class="things_width_full flex justify-between align-center form_box">
                <view class="flex justify-start align-center input_box">
                    <input class="withdraw_money" type="digit" placeholder="输入挂卖积分" :value="build_point" @input="buildPointInput" />
                </view>
                <button class="cu-btn submit_btn" :disabled="buttonType" @tap="buildPointsSaleFun">确定</button>
            </view>
            <view class="rule_tip">这里是交易提示说明</view>
        </view>
        <!-- 列表 -->
        <view class="hang_list">
            <!-- 导航栏 -->
            <scroll-view scroll-x class="bg-white nav">
                <view class="flex text-center navBox">
                    <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                        {{item.name}}
                    </view>
                </view>
            </scroll-view>
            <!-- 交易中-列表 -->
            <scroll-view v-if="tabCur == 1 && constructionList[0].list.length > 0" class="things_width_full transaction_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="flex justify-between align-center transaction_item" v-for="(item,index) in constructionList[0].list" :key="index">
                    <view class="transaction_item_left">
                        <view class="transaction_item_title">交易金额：{{item.value}}分</view>
                        <view class="transaction_item_time">{{item.create_time}}</view>
                    </view>
                    <button class="cu-btn bg-white transaction_item_right">取消</button>
                </view>
                 <view v-if="!constructionList[0].pageMore" class="flex justify-center align-center not_more">暂无更多</view>
            </scroll-view>
            <!-- 已完成-列表 -->
             <scroll-view v-if="tabCur == 2 && constructionList[1].list.length > 0" class="things_width_full transaction_list" scroll-y="true" @scrolltolower="getMoreData">
                <view class="flex justify-between align-center transaction_item" v-for="(item,index) in constructionList[1].list" :key="index">
                    <view class="transaction_item_left">
                        <view class="transaction_item_title">交易金额：{{item.value}}分</view>
                        <view class="transaction_item_time">{{item.complete_time}}</view>
                    </view>
                </view>
                <!-- 空数据 -->
                <view v-if="!constructionList[1].pageMore" class="flex justify-center align-center not_more">暂无更多</view>
            </scroll-view>
            <view v-if="tabCur == 1 && constructionList[0].list.length <= 0" class="flex justify-center align-center kong_data_box">
                <image class="kong_image" src="./../../../../../static/images/global/kong.png" mode="widthFix" />
                <view class="kong_text">暂无数据</view>
            </view>
            <view v-if="tabCur == 2 && constructionList[1].list.length <= 0" class="flex justify-center align-center kong_data_box">
                <image class="kong_image" src="./../../../../../static/images/global/kong.png" mode="widthFix" />
                <view class="kong_text">暂无数据</view>
            </view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { giftsPointsFraction_api,tradingLinkedSaleList_api,completedLinkedSaleList_api,buildPointsLinkedSale_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import storage from '@/utils/storage.js';
import { pointsType_format } from '@/utils/filters.js';
let that = null;
export default {
    data() {
        return {
            tabCur: 1,
            tabData: [
                {
                    id: 1,
                    name: '交易中'
                },
                {
                    id: 2,
                    name: '已完成'
                }
            ],
            // 用户数据
            userData: storage.get("userData") || {},
            build_point: '',// 挂卖积分数量
            userAmount: {},// 积分分数
            constructionList: [
                {
                    page: 1,
                    page_size: 10,
                    list: [],
                    pageMore: true,
                },
                {
                    page: 1,
                    page_size: 10,
                    list: [],
                    pageMore: true
                }
            ],
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.user_userData': function(val) {
            that.userData = val;
        },
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'user'
        ]),
        /**导航 选择
         * @description: 
         * @param {*} e
         * @return {*}
         */
        tabSelect(e) {
            that.tabCur = e.currentTarget.dataset.id;
        },
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        clearPageFun(e) {
            that.build_point = '';
            that.constructionList = [
                {
                    page: 1,
                    page_size: 10,
                    list: [],
                    pageMore: true,
                },
                {
                    page: 1,
                    page_size: 10,
                    list: [],
                    pageMore: true
                }
            ];
            that.getFraction();
            that.getConstructionSaleList();
            that.getConstructionCompletedList();
        },
        /**建设分挂卖
         * @description: 
         * @param {*} e
         * @return {*}
         */
        buildPointsSaleFun(e) {
            if (!that.build_point || Number(that.build_point) === 0) {
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'请输入挂售数量'}});
                return false;
            };
            that.buttonType = true;
            that.modal({name:'popLoading',value:true});
            let params = {
                build_point: that.build_point
            };
            buildPointsLinkedSale_api(params,that).then(res=>{
                that.modal({name:'popLoading',value:false});
                that.buttonType = false;
                that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'挂售成功'}});
                that.clearPageFun();
            }).catch(err=>{
                console.log(err);
                that.buttonType = false;
                that.modal({name:'popLoading',value:false});
            })
        },
        /**修改挂售建设分数量
         * @description: 
         * @param {*} e
         * @return {*}
         */
        buildPointInput(e) {
            that.build_point = e.detail.value;
        },
        /**获取积分分数
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFraction(e) {
            let params = {};
            giftsPointsFraction_api(params,that).then(res=>{
                that.userAmount = res.userAmount;
            }).catch(err=>{
                console.log(err);
            })

        },
        /**获取建设分交易中列表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getConstructionSaleList() {
            let params = {
                page: that.constructionList[0].page,
                page_size: that.constructionList[0].page_size,
            };
            tradingLinkedSaleList_api(params,that).then(res=>{
                that.constructionList[0].list = that.constructionList[0].list.concat(res.list);
                if (res.total > that.constructionList[0].list.length) {
                    that.constructionList[0].pageMore = true;
                    that.constructionList[0].page++;
                }else{
                    that.constructionList[0].pageMore = false;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        /**获取建设分已完成列表
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getConstructionCompletedList() {
            let params = {
                page: that.constructionList[1].page,
                page_size: that.constructionList[1].page_size,
            };
            completedLinkedSaleList_api(params,that).then(res=>{
                that.constructionList[1].list = that.constructionList[1].list.concat(res.list);
                if (res.total > that.constructionList[1].list.length) {
                    that.constructionList[1].pageMore = true;
                    that.constructionList[1].page++;
                }else{
                    that.constructionList[1].pageMore = false;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        /**获取更多数据
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        getMoreData(e) {
            if(that.tabCur == 1) {
                if (that.constructionList[0].pageMore) that.getConstructionSaleList();
            }else{
                if (that.constructionList[1].pageMore) that.getConstructionCompletedList();
            }
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
    },
    onShow: function() {
        that.clearPageFun();
    }
}
</script>
<style lang="scss" scoped>
    @import "./index.scss";
</style>